import React, {useState} from 'react';
import { Link } from "react-router-dom"
import {useNavigate} from "react-router-dom";

import './App.css';

const Mainpage = () => {
    const navigate = useNavigate();
    return (
        <div className="menubar">
            <button className="login" onClick={() => navigate("/member/login")}></button>
            <button className="login" onClick={() => navigate("/member/signup")}></button>
            <ul>
                <li onClick={() => navigate("/main")}></li>
                <li onClick={() => navigate("/animal")}>  </li>
                <li onClick={() => navigate("/recommend")}> </li>
                <li onClick={() => navigate("/board/list")}></li>
                <li onClick={() => navigate("/")}>  </li>
                <li className="dropdown">
                    <span> </span>
                    <div className="dropdown-content">
                        <Link to="/member/mypage">   </Link>
                        <Link to="/member/mypage"> </Link>
                        <Link to="/member/mypage">   </Link>
                    </div>
                </li>
            </ul>
        </div>
    );
};
export default Mainpage;